<style type="text/css">
  .applet-input {
    width: 190px;
  }

  .user-dialog .el-dialog {
    width: 650px;
  }
</style>
<template>
  <el-container>
    <el-main v-loading="loading" element-loading-text="" style="background-color: #FFFFFF;padding-top: 20px;">
      <el-form id="user-list-form" :inline="true" :model="info" ref="queryUserForm" class="demo-form-inline"
               style="text-align: left;">
        <el-form-item label="" prop="mobile">
          <el-input type="number" v-model="info.mobile" placeholder="" :clearable="true" class="applet-input"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="info.nickName" placeholder="" :clearable="true" class="applet-input"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ifRetail">
          <el-date-picker v-model="info.startDate" placeholder=""
                          :format="format" :value-format="valueFormat" style="width: 190px;">
          </el-date-picker>
          &nbsp;-&nbsp;
          <el-date-picker v-model="info.endDate" placeholder=""
                          :format="format" :value-format="valueFormat" style="width: 190px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="info.status" placeholder="" class="applet-input">
            <el-option label="" value=''></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="selectList"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="warning" @click="resetForm()"></el-button>
        </el-form-item>
        <div style="display: none;">
          <el-input v-model="info.page" type="hidden"></el-input>
          <el-input v-model="info.pageSize" type="hidden"></el-input>
        </div>
      </el-form>
      <el-table :data="tableData" :height="tableHeight" stripe style="width: 100%">
        <el-table-column align="center" type="index" :index="indexMethod" label=""
                         width="50"></el-table-column>
        <el-table-column align="center" prop="mobile" label="" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="nickName" label="" width="160"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="nickName" label="" width="50"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.gender ? '':''}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="birthday" label="" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="email" label="" width="160"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="balance" label="" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="freeBalance" label="" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="integral" label="" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column align="center" prop="status" label="" width="100"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-link type="success" :underline="false" v-if="scope.row.status"></el-link>
            <el-link type="warning" :underline="false" v-if="!scope.row.status"></el-link>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="createDate" label="" width="100"></el-table-column>
        <el-table-column align="center" fixed="right" label="">
          <template slot-scope="scope">
            <el-button type="primary" plain size="mini" @click="setStatus(scope.row.id)" v-if="scope.row.status">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: right;height: 35px;padding-top: 10px;padding-right: 30px;">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="info.page"
          :page-size="info.pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
      <el-dialog :title="showTitle" :visible.sync="showInfo" class="user-dialog" :modal-append-to-body="false"
                 :close-on-click-modal="false">
        <div style="overflow-x: hidden;overflow-y: auto;">
          <!--          <appletDetails ref="appletDetails" v-on:setAppletId="setAppletId"></appletDetails>-->
        </div>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script type="text/javascript">
  import appletDetails from '@/views/admin/applet/applet-details.vue'

  export default {
    name: 'applet-audit-list',
    components: {
      'appletDetails': appletDetails
    },
    data () {
      return {
        loading: true,
        tableHeight: 50,
        showInfo: false,
        showTitle: '',
        currentPage: 1,
        total: 0,
        info: {
          mobile: '',
          nickName: '',
          startDate: '',
          endDate: '',
          status: '',
          page: 1,
          pageSize: 15
        },
        format: 'yyyyMMdd',
        valueFormat: 'yyyy-MM-dd',
        rList: []
      }
    },
    created () {
      this.onLoad()
    },
    mounted () {
    },
    methods: {
      indexMethod (index) {
        let count = (parseInt(this.info.page) - 1) * parseInt(this.info.pageSize)
        return count + (parseInt(index) + 1)
      },
      onLoad () {
        this.loading = true
        this.$axios({
          url: '/api/manage/user/queryUserToPage',
          method: 'post',
          data: this.info
        }).then(res => {
          console.info('', res.data)
          this.$global.setTableHeight(this, 'user-list-form')
          if (res.data.code === '1') {
            this.tableData = res.data.data.dataSource
            this.total = res.data.data.totalCount
          } else if (res.data.code === '-1') {
            this.$message.error(res.data.data)
          }
          this.$global.exitLoad(this, null, res.data)
        }).catch(error => {
          console.info('', error)
          this.$global.exitLoad(this, null, '')
        })
      },
      selectList () {
        this.info.page = 1
        this.showInfo = false
        this.onLoad()
      },
      handleCurrentChange (val) {
        this.info.page = val
        this.onLoad()
      },
      resetForm () {
        this.info.startDate = ''
        this.info.endDate = ''
        this.$refs['queryUserForm'].resetFields()
      },
      setStatus (appletId, appletName) {

      },
      setAppletId () {
        this.selectList()
      }
    }
  }
</script>



