<script type='text/javascript'>
  import BScroll from 'better-scroll'

  let partList = []

  const storeList = [
    // {
    //   'id': '',
    //   'name': '',
    //   'title': '',
    //   'describe': '',
    //   'list': [{
    //     'typeId': '',
    //     'goodsId': '',
    //     'name': '',
    //     'minPrice': 0.00,
    //     'maxPrice': 0.00,
    //     'goodsIcon': '',
    //     'icon': ''
    //   }]
    // },

    {
      'id': 'goods-search',
      'name': ''
    }, {
      'id': 'img-carousel',
      'name': '',
      'list': [{
        'goodsId': '',
        'goodsIcon': '',
        'icon': '/static/images/carousel/carousel-1.png'
      }]
    }, {
      'id': 'notice-bar',
      'name': '',
      'describe': ''
    }, {
      'id': 'type-frame-three',
      'name': '',
      'list': [{
        'typeId': '0',
        'name': 'type',
        'icon': '/static/images/icon/shopping.png'
      }]
    }, {
      'id': 'type-frame-four',
      'name': '',
      'list': [{
        'typeId': '0',
        'name': 'type',
        'icon': '/static/images/icon/shopping.png'
      }]
    }, {
      'id': 'img-text-title',
      'name': '',
      'title': '',
      'describe': '',
      'list': [{
        'icon': '/static/images/icon/shopping.png'
      }]
    }, {
      'id': 'goods-two-row',
      'name': '',
      'list': [{
        'goodsId': '0',
        'name': '',
        'minPrice': 166.66,
        'maxPrice': 188.88,
        'discount': 100,
        'goodsIcon': '/static/images/goods/goods-1.png'
      }]
    }, {
      'id': 'divider-title',
      'name': '',
      'title': ''
    }, {
      'id': 'img-title',
      'name': '',
      'list': [{
        'goodsId': '',
        'goodsIcon': '',
        'icon': '/static/images/carousel/carousel-1.png'
      }]
    }, {
      'id': 'goods-three-row',
      'name': '',
      'list': [{
        'goodsId': '0',
        'name': '',
        'minPrice': 166.66,
        'maxPrice': 188.88,
        'discount': 100,
        'goodsIcon': '/static/images/goods/goods-1.png'
      }]
    }, {
      'id': 'imgX-scroll',
      'name': '',
      'list': [{
        'goodsId': '',
        'goodsIcon': '',
        'icon': '/static/images/carousel/carousel-1.png'
      }]
    }, {
      'id': 'imgY-scroll',
      'name': '',
      'list': [{
        'goodsId': '',
        'goodsIcon': '',
        'icon': '/static/images/carousel/carousel-1.png'
      }]
    }, {
      'id': 'goodsY-scroll',
      'name': '',
      'list': [{
        'goodsId': '0',
        'name': '',
        'minPrice': 166.66,
        'maxPrice': 188.88,
        'discount': 100,
        'goodsIcon': '/static/images/goods/goods-1.png'
      }]
    }, {
      'id': 'goodsX-scroll',
      'name': '',
      'list': [{
        'goodsId': '0',
        'name': '',
        'minPrice': 166.66,
        'maxPrice': 188.88,
        'discount': 100,
        'goodsIcon': '/static/images/goods/goods-1.png'
      }]
    }
  ]

  let getPartList = function () {
    return partList
  }

  let setPartList = function (list) {
    partList = list
  }

  let getPart = function (id) {
    let obj = partList.find((item) => {
      return item.id === id
    })
    return obj
  }

  let getStoreList = function () {
    return storeList
  }

  let getStorePart = function (id) {
    let obj = storeList.find((item) => {
      return item.id === id
    })
    return obj
  }

  let _initStoreScroll = function (that) {
    try {
      that.$nextTick(() => {
        let part = getPart('imgX-scroll')
        if (part) {
          let imgXLength = part.list.length
          that.imgXScrollStyle = {
            'width': (240 * imgXLength) + 'px'
          }
          that.aBScroll = new BScroll(that.$refs.imgXScroll[0], {
            startX: 0,
            scrollX: true,
            click: true
          })
        }
        part = getPart('imgY-scroll')
        if (part) {
          let imgYLength = part.list.length
          that.imgYScrollStyle = {
            'width': (140 * imgYLength) + 'px'
          }
          that.aBScroll = new BScroll(that.$refs.imgYScroll[0], {
            startX: 0,
            scrollX: true,
            click: true
          })
        }
        part = getPart('goodsY-scroll')
        if (part) {
          let goodsYLength = part.list.length
          that.goodsYScrollStyle = {
            'width': (220 * goodsYLength) + 'px'
          }
          that.aBScroll = new BScroll(that.$refs.goodsYScroll[0], {
            startX: 0,
            scrollX: true,
            click: true
          })
        }
        part = getPart('goodsX-scroll')
        if (part) {
          let goodsXLength = part.list.length
          that.goodsXScrollStyle = {
            'width': (280 * goodsXLength) + 'px'
          }
          that.aBScroll = new BScroll(that.$refs.goodsXScroll[0], {
            startX: 0,
            scrollX: true,
            click: true
          })
        }
      })
    } catch (e) {
      console.info('', e)
    }
  }

  export default {
    getPartList,
    setPartList,
    getStoreList,
    getStorePart,
    _initStoreScroll
  }
</script>



